"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface Kriteria {
  id: number
  nama: string
  bobot: number
  kategori: 'cost' | 'benefit'
}

export default function KriteriaPage() {
  const [kriterias, setKriterias] = useState<Kriteria[]>([])
  const [newKriteria, setNewKriteria] = useState({
    nama: "",
    bobot: "",
    kategori: "benefit" as 'cost' | 'benefit'
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const id = kriterias.length + 1
    setKriterias([...kriterias, { 
      ...newKriteria, 
      id,
      bobot: parseFloat(newKriteria.bobot)
    }])
    setNewKriteria({ nama: "", bobot: "", kategori: "benefit" })
  }

  return (
    <div className="p-6">
      <div className="max-w-7xl mx-auto">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold">Data Kriteria</h1>
          <Dialog>
            <DialogTrigger asChild>
              <Button>Tambah Kriteria</Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Tambah Data Kriteria</DialogTitle>
              </DialogHeader>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <Label htmlFor="nama">Nama Kriteria</Label>
                  <Input
                    id="nama"
                    value={newKriteria.nama}
                    onChange={(e) =>
                      setNewKriteria({ ...newKriteria, nama: e.target.value })
                    }
                    placeholder="Masukkan nama kriteria"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="bobot">Bobot</Label>
                  <Input
                    id="bobot"
                    type="number"
                    step="0.01"
                    min="0"
                    max="1"
                    value={newKriteria.bobot}
                    onChange={(e) =>
                      setNewKriteria({ ...newKriteria, bobot: e.target.value })
                    }
                    placeholder="Masukkan bobot (0-1)"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="kategori">Kategori</Label>
                  <Select
                    value={newKriteria.kategori}
                    onValueChange={(value: 'cost' | 'benefit') =>
                      setNewKriteria({ ...newKriteria, kategori: value })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih kategori" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="benefit">Benefit</SelectItem>
                      <SelectItem value="cost">Cost</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <Button type="submit" className="w-full">
                  Simpan
                </Button>
              </form>
            </DialogContent>
          </Dialog>
        </div>

        <Card>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>No</TableHead>
                <TableHead>Nama Kriteria</TableHead>
                <TableHead>Bobot</TableHead>
                <TableHead>Kategori</TableHead>
                <TableHead>Aksi</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {kriterias.map((kriteria) => (
                <TableRow key={kriteria.id}>
                  <TableCell>{kriteria.id}</TableCell>
                  <TableCell>{kriteria.nama}</TableCell>
                  <TableCell>{kriteria.bobot}</TableCell>
                  <TableCell className="capitalize">{kriteria.kategori}</TableCell>
                  <TableCell>
                    <Button variant="ghost" size="sm">
                      Edit
                    </Button>
                    <Button variant="ghost" size="sm" className="text-red-600">
                      Hapus
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
              {kriterias.length === 0 && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center">
                    Belum ada data kriteria
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </Card>
      </div>
    </div>
  )
}
